import Vue from 'vue'
import App from './App.vue'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import { Message } from 'element-ui';
import MQTT from 'mqtt'

Vue.use(ElementUI);
Vue.prototype.$message = Message;
Vue.config.productionTip = false
Vue.prototype.$Mqtt = function (url, title, name, pass) {
  let client
  const options = {
    connectTimeout: 40000, // 毫秒
    clientId: 'mqtitId-Home',
    username: name, // 用户名
    password: pass, //密码
    clean: true
  }
  if (url!==undefined) {
    client = MQTT.connect('ws://' + url + '/mqtt', options)
    client.on('connect', (e) => {
      console.log(e)
      console.log("连接成功！！！")
      client.subscribe(title, {qos: 0}, (error) => {
        if (!error) {
          console.log('订阅成功')
        } else {
          console.log('订阅失败')
        }
      })

    })
    // 接收消息处理
    client.on('message', (topic, message) => {
      console.log('收到来自', topic, '的消息', message.toString())
      let data = JSON.parse(message.toString())
      this.msg = data.v2
      console.log(data)
      Vue.prototype.$message({
        message: "获取到了消息"
      });
    })
  }
}

new Vue({
  render: h => h(App),
}).$mount('#app')
