<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <i @click="reader" class="el-icon-microphone"></i>
<!--    <div id="group">{{words}}</div>-->
<!--    <el-button @click="msgClick">MQTT</el-button>-->
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  data () {
    return {
      words:'啵啵啵，喜欢小唐，哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈哈'
    }
  },
  created() {
    // this.$Mqtt('zhsq.xcuni.com:10314','test','admin','123.com')
    this.reader()
  },
  methods:{
    msgClick(){
      this.$Mqtt('zhsq.xcuni.com:10314','test','admin','123.com')
    },
    reader(){
      let url = "http://tts.baidu.com/text2audio?lan=zh&ie=UTF-8&text=" + encodeURI(this.words);
      let n = new Audio(url);
      n.src = url;
      n.play();  //播放阅读
    //n.cancel();  //取消阅读
    //n.pause();  //暂停阅读
    }
  }
}
</script>

